body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eeeded;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
  width: 100%;
  padding: 5px;
  background-color: #4082f5;
  font-size: 40px;
  text-align: center;
  color: white;
}

.simple-keyboard.hg-layout-default .hg-button.button-green {
  background: green;
}

.simple-keyboard.hg-layout-default .hg-button.button-yellow {
  background: yellow;
}

.simple-keyboard.hg-layout-default .hg-button.button-gray {
  background: gray;
}

.simple-keyboard {
  font-weight: 500;
}